.reset();

/* Цвета */
@green: #5d9a01;
@black: #000;
@white: #fff;

html{
    font-size: 10px;
}

body{
    font-family: 'Montserrat', sans-serif;
}

.wrapper{
    .wrapper();
}

.bold(){
    font-weight: 600;
}

.factory{
    background: url('../img/audit.png') no-repeat center;
    background-size: cover;
}

.trucks{
    background: url('../img/trash.png') no-repeat center;
    background-size: cover;
}

button{
    outline: none;
    border: none;
    cursor: pointer;
}

.prev, .next{
    background: transparent;
    border: none;
    cursor: pointer;
}

.hvr-fade:hover,.hvr-fade:focus{
    background: #3d6501;
}




