body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

/* Цвета */

html {
  font-size: 10px;
}

body {
  font-family: 'Montserrat', sans-serif;
}

.wrapper {
  max-width: 1170px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.factory {
  background: url('../img/audit.png') no-repeat center;
  background-size: cover;
}

.trucks {
  background: url('../img/trash.png') no-repeat center;
  background-size: cover;
}

button {
  outline: none;
  border: none;
  cursor: pointer;
}

.prev,
.next {
  background: transparent;
  border: none;
  cursor: pointer;
}

.hvr-fade:hover,
.hvr-fade:focus {
  background: #3d6501;
}

/* Header */

.header {
  background: #fff;
  padding: 2rem 0;
}

.header-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header-name {
  width: calc(50% - 30px);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-name-logo {
  border-right: 1px solid #b8b8b8;
  padding-right: 1.5rem;
}

.header-name-descr {
  font-size: 1.4rem;
  letter-spacing: 0.07px;
  padding-left: 2rem;
}

.header-contacts {
  width: calc(41.66666666666667% - 30px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  cursor: pointer;
}

.header-contacts-phone {
  font-size: 1.8rem;
  text-transform: uppercase;
  letter-spacing: 0.06px;
}

.header-contacts-phone i {
  color: #5d9a01;
}

.header-contacts-mail {
  font-size: 1.4rem;
  color: #929292;
  letter-spacing: 0.05px;
}

.header-contacts-btn {
  background: #5d9a01;
  border-radius: 0.5rem;
  text-transform: uppercase;
  color: #fff;
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 0.06px;
  border: none;
  padding: 1rem 2rem;
}

.header-contacts-item {
  text-align: center;
}

/*Главный экран*/

.main {
  background: url('../img/index.png') no-repeat center;
  background-size: cover;
}

.main-container {
  padding: 9rem 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.main-titles {
  width: calc(50% - 30px);
}

.main-title {
  padding-bottom: 8rem;
  color: #fff;
  font-size: 5.5rem;
  font-weight: 600;
}

.main-subtitles-item {
  font-size: 1.8rem;
  color: #fff;
  padding-bottom: 2rem;
}

.main-form {
  width: calc(41.66666666666667% - 30px);
  text-align: center;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  padding: 2rem 1rem;
}

.main-form-title {
  font-size: 2.4rem;
  text-transform: uppercase;
  font-weight: 600;
}

.main-form-subtitle {
  font-size: 1.6rem;
  padding-top: 2rem;
}

.main-form form {
  margin: 2rem 0;
}

.main-form form input {
  display: block;
  width: 80%;
  height: 6rem;
  margin: 0 auto;
  border-radius: 10px;
  border: 1px solid #d8d8d8;
  margin-bottom: 1.5rem;
  outline: none;
  font-size: 1.6rem;
  padding-left: 2.5rem;
}

.main-form form button {
  padding: 2rem 7rem;
  width: 80%;
  background-color: #5d9a00;
  border-radius: 10px;
  font-size: 1.6rem;
  color: #fff;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
}

.main-form-footnote {
  padding-top: 1rem;
  font-size: 1.2rem;
  color: #000000;
  opacity: 0.5;
  font-weight: 600;
}

/*Услуги*/

.services-items {
  padding: 7rem 0 4rem 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.services-item {
  width: calc(41.66666666666667% - 30px);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 6rem 5rem;
}

.services-item-title {
  font-size: 4.5rem;
  font-weight: 600;
  color: #fff;
}

.services-item-footer {
  padding-top: 6.5rem;
}

.services-item-descr {
  font-size: 1.6rem;
  color: #fff;
}

.services-item-btn {
  margin-top: 4rem;
  background-color: #5d9a01;
  border-radius: 5px;
  font-size: 1.2rem;
  text-transform: uppercase;
  color: #ffffff;
  padding: 1rem 3rem;
  border: none;
  cursor: pointer;
}

/*Факты*/

.facts {
  margin-top: 7rem;
  padding-top: 9rem;
  padding-bottom: 9rem;
  background-color: #f7fafb;
}

.facts-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.facts-descr {
  width: calc(33.333333333333336% - 30px);
}

.facts-descr-title {
  font-size: 5rem;
  font-weight: 600;
  padding-bottom: 4rem;
}

.facts-descr-subtitle {
  font-size: 1.8rem;
}

.facts-items {
  width: calc(66.66666666666667% - 30px);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.facts-item {
  width: calc(50% - 30px);
  padding: 1.2rem;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 3rem;
  border: 1px solid #ebecec;
}

.facts-item-descr {
  font-size: 1.4rem;
}

/*Лицензии*/

.license-title {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(41.66666666666667% - 30px);
  padding-top: 10rem;
  padding-bottom: 6rem;
  font-size: 5rem;
  font-weight: 600;
}

.license-subtitle {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(75% - 30px);
  font-size: 1.8rem;
  padding-bottom: 7rem;
}

.license-slider-item {
  outline: none;
}

.license-slider-item img {
  display: block;
  margin: 0 auto;
}

.prev {
  position: absolute;
  top: 50%;
  left: -2%;
  transform: translateY(-50%);
  z-index: 99;
  width: 5rem;
  height: 5rem;
  border: none;
  background: url('../img/prev.png') center no-repeat;
  background-size: 70%;
  cursor: pointer;
}

.next {
  position: absolute;
  top: 50%;
  right: -2%;
  transform: translateY(-50%);
  z-index: 99;
  width: 5rem;
  height: 5rem;
  border: none;
  background: url('../img/next.png') center no-repeat;
  background-size: 70%;
  cursor: pointer;
}

.license-btn {
  width: calc(33.333333333333336% - 30px);
  display: block;
  margin: 6.5rem auto 8rem auto;
  padding: 2rem 0;
  background: #5d9a01;
  border-radius: 1rem;
  font-size: 1.6rem;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 600;
}

/*Футер*/

.footer {
  background: url('../img/footer-bg.jpg') no-repeat center;
  background-size: cover;
}

.footer-title {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(58.333333333333336% - 30px);
  font-size: 4rem;
  font-weight: 600;
  padding-top: 9rem;
}

.footer-subtitle {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(58.333333333333336% - 30px);
  font-size: 1.8rem;
  padding: 4.5rem 0;
}

.footer-form {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(66.66666666666667% - 30px);
  margin-bottom: 4rem;
}

.footer-form-input {
  width: calc(41.66666666666667% - 30px);
  height: 6rem;
  margin-right: 3rem;
  border: 1px solid #d8d8d8;
  border-radius: 10px;
  padding-left: 2.5rem;
  font-size: 1.6rem;
  outline: none;
}

.footer-form-btn {
  width: calc(50% - 30px);
  font-weight: 600;
  margin-top: 2rem;
  padding: 2.5rem 0;
  display: block;
  background: #5d9a01;
  border-radius: 10px;
  font-size: 1.6rem;
  color: #ffffff;
  text-transform: uppercase;
}

.footer-phone {
  font-size: 4rem;
  font-weight: 600;
  padding-bottom: 2.5rem;
}

.footer-mail {
  font-size: 2.4rem;
  font-weight: 600;
  padding-bottom: 6.5rem;
}

/*Всплывающие окна*/

/*Форма*/

.popup-order {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup-order-content {
  position: fixed;
  text-align: center;
  left: 50%;
  top: 10%;
  transform: translateX(-50%);
  padding: 2rem 4rem;
  background: #fff;
  width: calc(33.333333333333336% - 30px);
}

.popup-order-close {
  position: absolute;
  top: -1rem;
  right: -2rem;
  width: 2rem;
  text-align: center;
  border: none;
  background-color: transparent;
  font-size: 3rem;
  color: #fff;
  cursor: pointer;
}

.popup-order-form {
  padding: 4rem 0;
  background: #fff;
  margin: 0 auto;
}

.popup-order-form-input {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: 55px;
  margin-bottom: 2rem;
  border: 1px solid #dddddd;
  border-radius: 10px;
  font-size: 1.6rem;
  text-align: center;
  outline: none;
}

.popup-order-form-btn {
  width: 100%;
  padding: 2.5rem 0;
  display: block;
  background: #5d9a01;
  border-radius: 10px;
  font-size: 1.5rem;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 600;
}

.popup-order-header {
  font-size: 1.8rem;
  font-weight: 600;
}

/*Спасибо за обращение*/

.popup-thanks {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup-thanks-content {
  position: fixed;
  text-align: center;
  left: 50%;
  top: 35%;
  transform: translateX(-50%);
  padding: 3rem 5rem 3rem 5rem;
  background: #fff;
  width: calc(25% - 30px);
}

.popup-thanks-close {
  position: absolute;
  top: -1rem;
  right: -2rem;
  width: 2rem;
  text-align: center;
  border: none;
  background-color: transparent;
  font-size: 3rem;
  color: #fff;
  cursor: pointer;
}

.popup-thanks-title {
  font-size: 1.8rem;
  font-weight: 600;
  padding-bottom: 2rem;
}

.popup-thanks-subtitle {
  font-size: 1.8rem;
  font-weight: 600;
}

.popup-thanks-icon {
  padding-bottom: 1.5rem;
  font-size: 4rem;
  color: #5d9a01;
}

/*Экологический аудит*/

.popup-audit {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup-audit-content {
  position: fixed;
  left: 50%;
  top: 25%;
  transform: translateX(-50%);
  padding: 2rem 5rem 2rem 5rem;
  background: #fff;
  width: calc(50% - 30px);
}

.popup-audit-close {
  position: absolute;
  top: -1rem;
  right: -2rem;
  width: 2rem;
  text-align: center;
  border: none;
  background-color: transparent;
  font-size: 3rem;
  color: #fff;
  cursor: pointer;
}

.popup-audit-title {
  font-size: 3rem;
  text-align: center;
  font-weight: 600;
  padding-bottom: 1.5rem;
}

.popup-audit-subtitle {
  text-align: center;
  font-size: 2rem;
}

.popup-audit-items {
  margin: 2rem 0;
}

.popup-audit-item {
  font-size: 1.6rem;
  padding-bottom: 1.5rem;
}

.popup-audit-item i {
  font-size: 2.1rem;
  color: #5d9a01;
}

.popup-audit-price {
  font-size: 1.8rem;
  font-weight: 600;
}

.popup-audit-btn {
  display: block;
  margin: 2rem auto 0 auto;
  width: 40%;
  background: #5d9a01;
  border-radius: 1rem;
  height: 5rem;
  font-size: 2rem;
  color: #ffffff;
}

/*Вывоз мусора*/

.popup-trash {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup-trash-content {
  position: fixed;
  left: 50%;
  top: 25%;
  transform: translateX(-50%);
  padding: 2rem 5rem 2rem 5rem;
  background: #fff;
  width: calc(50% - 30px);
}

.popup-trash-close {
  position: absolute;
  top: -1rem;
  right: -2rem;
  width: 2rem;
  text-align: center;
  border: none;
  background-color: transparent;
  font-size: 3rem;
  color: #fff;
  cursor: pointer;
}

.popup-trash-title {
  font-size: 3rem;
  text-align: center;
  font-weight: 600;
  padding-bottom: 1.5rem;
}

.popup-trash-subtitle {
  text-align: center;
  font-size: 2rem;
  padding-bottom: 2rem;
}

.popup-trash-items {
  margin-bottom: 2rem;
}

.popup-trash-item {
  font-size: 1.6rem;
  padding-bottom: 1.5rem;
}

.popup-trash-item i {
  font-size: 2.1rem;
  color: #5d9a01;
}

.popup-trash-price {
  font-size: 1.8rem;
  font-weight: 600;
}

.popup-trash-btn {
  display: block;
  margin: 2rem auto 0 auto;
  width: 40%;
  background: #5d9a01;
  border-radius: 1rem;
  height: 5rem;
  font-size: 2rem;
  color: #ffffff;
}

