@import 'smart-grid.less';
@import 'settings.less';

/* Header */
.header{
    background: #fff;
    padding: 2rem 0;
}

.header-container{
    display: flex;
    .justify-content(space-between);
    .justify-content-md(center);
    flex-wrap: wrap;
}

.header-name{
    .size(6);
    .size-md(12);
    display: flex;
    .justify-content(space-between);
    .justify-content-md(center);
    align-items: center;

    &-logo{
        border-right: 1px solid #b8b8b8;
        padding-right: 1.5rem;
    }

    &-descr{
        font-size: 1.4rem;
        letter-spacing: 0.07px;
        padding-left: 2rem;
    }
}

.header-contacts{
    .size(5);
    .size-md(12);
    display: flex;
    .justify-content(space-between);
    .justify-content-md(center);
    align-items: center;
    flex-wrap: wrap;
    

    &-phone{
        font-size: 1.8rem;
        text-transform: uppercase;
        letter-spacing: 0.06px;

        i{
            color: @green;
        }
    }

    &-mail{
        font-size: 1.4rem;
        color: #929292;
        letter-spacing: 0.05px;
    }

    &-btn{
        background: @green;
        border-radius: 0.5rem;
        text-transform: uppercase;
        color: #fff;
        font-size: 1.2rem;
        .bold();
        letter-spacing: 0.06px;
        border: none;
        padding: 1rem 2rem;
        }
        cursor: pointer;
}

.header-contacts-item{
    text-align: center;
}

/*Главный экран*/

.main{
    background: url('../img/index.png') no-repeat center;
    background-size: cover;
}

.main-container{
    padding: 9rem 0;
    display: flex;
    flex-wrap: wrap;
    .justify-content(space-between);
    .justify-content-md(center);
}

.main-titles{
    .size(6);
    .size-md(10);
    .size-xs(12);

}

.main-title{
    padding-bottom: 8rem;
    color: #fff;
    font-size: 5.5rem;
    .bold();
}

.main-subtitles-item{
    font-size: 1.8rem;
    color: #fff;
    padding-bottom: 2rem;
}

.main-form{
    .size(5);
    .size-md(7);
    .size-sm(8);
    .size-xs(11);
    text-align: center;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    padding: 2rem 1rem;

    &-title{
        font-size: 2.4rem;
        text-transform: uppercase;
        .bold();
    }

    &-subtitle{
        font-size: 1.6rem;
        padding-top: 2rem;
    }

    form{
        margin: 2rem 0;
        input{
            display: block;
            width: 80%;
            height: 6rem;
            margin: 0 auto;
            border-radius: 10px;
            border: 1px solid #d8d8d8;
            margin-bottom: 1.5rem;
            outline: none;
            font-size: 1.6rem;
            padding-left: 2.5rem;
        }

        button{
            padding: 2rem 7rem;
            width: 80%;
            background-color: #5d9a00;
            border-radius: 10px;
            font-size: 1.6rem;
            color: #fff;
            text-transform: uppercase;
            border: none;
            cursor: pointer;
        }

    }

    &-footnote{
        padding-top: 1rem;
        font-size: 1.2rem;
        color: #000000;
        opacity: 0.5;
        .bold();
    }
}

/*Услуги*/

.services-items{
    padding: 7rem 0 4rem 0;
    display: flex;
    .justify-content(space-between);
    .justify-content-md(center);
    flex-wrap: wrap;
}

.services-item{
    .size(5);
    .size-md(8);
    .size-sm(10);
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 6rem 5rem;
    
    &-title{
        font-size: 4.5rem;
        .bold();
        color: #fff;
    }
    
     &-footer{
         padding-top: 6.5rem;
     }

    &-descr{
        font-size: 1.6rem;
        color: #fff;
    }

    &-btn{
        margin-top: 4rem;
        background-color: @green;
        border-radius: 5px;
        font-size: 1.2rem;
        text-transform: uppercase;
        color: #ffffff;
        padding: 1rem 3rem;
        border: none;
        cursor: pointer;
    }
}

/*Факты*/
.facts{
    margin-top: 7rem;
    padding-top: 9rem;
    padding-bottom: 9rem;
    background-color: #f7fafb;
}

.facts-container{
    display: flex;
    .justify-content(space-between);
    .justify-content-md(center);
    flex-wrap: wrap;
}

.facts-descr{
    .size(4);
    .size-md(10);
    .size-xs(12);

    &-title{
        font-size: 5rem;
        .bold();
        padding-bottom: 4rem;
    }

    &-subtitle{
        font-size: 1.8rem;
    }
}

.facts-items{
    .size(8);
    .size-md(10);
    .size-xs(11);
    display: flex;
    flex-wrap: wrap;
    .justify-content(space-between);
    .justify-content-sm(center);
}

.facts-item{
    .size(6);
    .size-sm(10);
    .size-xs(12);
    padding: 1.2rem;
    background: #fff;
    border-radius: 10px;
    margin-bottom: 3rem;
    border: 1px solid #ebecec;

    &-descr{

        font-size: 1.4rem;
    }
}

/*Лицензии*/

.license-title{
    .col();
    .size(5);
    .size-md(12);
    padding-top: 10rem;
    padding-bottom: 6rem;
    font-size: 5rem;
    .bold();
}

.license-subtitle{
    .col();
    .size(9);
    .size-md(12);
    font-size: 1.8rem;
    padding-bottom: 7rem;
}

.license-slider-item{
    outline: none;
    
    img{
        display: block;
        margin: 0 auto;
    }
}

.prev{
    position: absolute;
    top: 50%;
    left: -2%;
    transform: translateY(-50%);
    z-index: 99;
    width: 5rem;
    height: 5rem;
    border: none;
    background: url('../img/prev.png') center no-repeat;
    background-size: 70%;
    cursor: pointer;
}

.next{
    position: absolute;
    top: 50%;
    right: -2%;
    transform: translateY(-50%);
    z-index: 99;
    width: 5rem;
    height: 5rem;
    border: none;
    background: url('../img/next.png') center no-repeat;
    background-size: 70%;
    cursor: pointer;
}

.license-btn{
    .size(4);
    .size-md(5);
    .size-sm(6);
    .size-xs(11);
    display: block;
    margin: 6.5rem auto 8rem auto;
    padding: 2rem 0;
    background: @green;
    border-radius: 1rem;
    font-size: 1.6rem;
    color: #ffffff;
    text-transform: uppercase;
    .bold();
}

/*Футер*/
.footer{
    background: url('../img/footer-bg.jpg') no-repeat center;
    background-size: cover;
}

.footer-title{
    .col();
    .size(7);
    .size-md(8);
    .size-sm(12);
    font-size: 4rem;
    .bold();
    padding-top: 9rem;
}

.footer-subtitle{
    .col();
    .size(7);
    .size-md(8);
    .size-sm(12);
    font-size: 1.8rem;
    padding: 4.5rem 0;
}

.footer-form{
    .col();
    .size(8);
    .size-md(12);
    margin-bottom: 4rem;

    &-input{
        .size(5);
        .size-md(5);
        .size-sm(8);
        .size-xs(12);
        height: 6rem;
        margin-right: 3rem;
        border: 1px solid #d8d8d8;
        border-radius: 10px;
        padding-left: 2.5rem;
        font-size: 1.6rem;
        outline: none;
    }

    &-btn{
        .size(6);
        .size-md(6);
        .size-xs(12);
        .bold();
        margin-top: 2rem;
        padding: 2.5rem 0;
        display: block;
        background: @green;
        border-radius: 10px;
        font-size: 1.6rem;
        color: #ffffff;
        text-transform: uppercase;
    }
}

.footer-phone{
    font-size: 4rem;
    .bold();
    padding-bottom: 2.5rem;
}

.footer-mail{
    font-size: 2.4rem;
    .bold();
    padding-bottom: 6.5rem;
}

/*Всплывающие окна*/
/*Форма*/
.popup-order{
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.5);

    &-content {
        position: fixed;
        text-align: center;
        left: 50%;
        top: 10%;
        transform: translateX(-50%);
        padding: 2rem 4rem;
        background: #fff;
        .size(4);
        .size-md(5);
        .size-sm(7);
        .size-xs(10);
    }

    &-close {
        position: absolute;
        top: -1rem;
        right: -2rem;
        width: 2rem;
        text-align: center;
        border: none;
        background-color: transparent;
        font-size: 3rem;
        color: #fff;
        cursor: pointer;
    }
}

.popup-order-form{
    padding: 4rem 0;
    background: #fff;
    margin: 0 auto;

    &-input {
        display: block;
        margin: 0 auto;
        width: 100%;
        height: 55px;
        margin-bottom: 2rem;
        border: 1px solid #dddddd;
        border-radius: 10px;
        font-size: 1.6rem;
        text-align: center;
        outline: none;
    }

    &-btn{
        width: 100%;
        padding: 2.5rem 0;
        display: block;
        background: @green;
        border-radius: 10px;
        font-size: 1.5rem;
        color: #ffffff;
        text-transform: uppercase;
        .bold();
    }
}

.popup-order-header{
    font-size: 1.8rem;
    .bold();
}
/*Спасибо за обращение*/
.popup-thanks{
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.5);

    &-content {
        position: fixed;
        text-align: center;
        left: 50%;
        top: 35%;
        transform: translateX(-50%);
        padding: 3rem 5rem 3rem 5rem;
        background: #fff;
        .size(3);
        .size-md(4);
        .size-sm(6);
        .size-xs(8);
    }

    &-close {
        position: absolute;
        top: -1rem;
        right: -2rem;
        width: 2rem;
        text-align: center;
        border: none;
        background-color: transparent;
        font-size: 3rem;
        color: #fff;
        cursor: pointer;
    }

    &-title{
        font-size: 1.8rem;
        .bold();
        padding-bottom: 2rem;    
    }

    &-subtitle{
        font-size: 1.8rem;
        .bold();
    }

    &-icon{
        padding-bottom: 1.5rem;
        font-size: 4rem;
        color: @green;
    }
}
/*Экологический аудит*/
.popup-audit{
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.5);

    &-content {
        position: fixed;
        left: 50%;
        top: 25%;
        transform: translateX(-50%);
        padding: 2rem 5rem 2rem 5rem;
        background: #fff;
        .size(6);
        .size-md(8);
        .size-sm(9);
        .size-xs(9);
    }

    &-close {
        position: absolute;
        top: -1rem;
        right: -2rem;
        width: 2rem;
        text-align: center;
        border: none;
        background-color: transparent;
        font-size: 3rem;
        color: #fff;
        cursor: pointer;
    }

    &-title{
        font-size: 3rem;
        text-align: center;
        .bold();
        padding-bottom: 1.5rem;    
    }    

    &-subtitle{
        text-align: center;
        font-size: 2rem;
    }

    &-items{
        margin: 2rem 0;
    }

    &-item{
        font-size: 1.6rem;
        padding-bottom: 1.5rem;


        i{
            font-size: 2.1rem;
            color: @green;
        }
    }

    &-price{
        font-size: 1.8rem;
        .bold();
    }

    &-btn{
        display: block;
        margin: 2rem auto 0 auto;
        width: 40%;
        background: @green;
        border-radius: 1rem;
        height: 5rem;
        font-size: 2rem;
        color: #ffffff;
    }
}
/*Вывоз мусора*/
.popup-trash{
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.5);

    &-content {
        position: fixed;
        left: 50%;
        top: 25%;
        transform: translateX(-50%);
        padding: 2rem 5rem 2rem 5rem;
        background: #fff;
        .size(6);
        .size-md(8);
        .size-sm(9);
        .size-xs(9);
    }

    &-close {
        position: absolute;
        top: -1rem;
        right: -2rem;
        width: 2rem;
        text-align: center;
        border: none;
        background-color: transparent;
        font-size: 3rem;
        color: #fff;
        cursor: pointer;
    }

    &-title{
        font-size: 3rem;
        text-align: center;
        .bold();
        padding-bottom: 1.5rem;    
    }    

    &-subtitle{
        text-align: center;
        font-size: 2rem;
        padding-bottom: 2rem;
    }

    &-items{
        margin-bottom: 2rem;;
    }

    &-item{
        font-size: 1.6rem;
        padding-bottom: 1.5rem;


        i{
            font-size: 2.1rem;
            color: @green;
        }
    }

    &-price{
        font-size: 1.8rem;
        .bold();
    }

    &-btn{
        display: block;
        margin: 2rem auto 0 auto;
        width: 40%;
        background: @green;
        border-radius: 1rem;
        height: 5rem;
        font-size: 2rem;
        color: #ffffff;
    }
}


@media screen and (max-width: 992px){
    .header-contacts{
        margin-top: 3rem;
    }
    .header-contacts-item{
        margin-right: 4rem;
    }

    .main-form{
        margin-top: 3rem;
         form{
             input{
                 width: 90%;
                 font-size: 1.4rem;
             }

             button{
                 font-size: 1.4rem;
                 width: 97%;
             }
         }
    }

    .services-item{
        margin-bottom: 3rem;
    }

    .facts-descr{
        text-align: center;
        margin-bottom: 4rem;
    }

    .license-title, .license-subtitle{
        text-align: center;
    }
}

@media screen and (max-width: 720px){
    .facts-item{
        text-align: center;
    }

    .footer{
        text-align: center;
    }

    .footer-form-btn{
        .col();
        margin: 2.5rem auto 2.5rem auto;
    }

    .footer-form-input{
        .col();
        margin-bottom: 2rem;
    }
}

@media screen and (max-width: 576px){
    .header-name{
        &-logo{
            border-right: none;
        }

        &-descr{
            display: none;
        }
    }

    .header-contacts-item{
        margin: 0;
        margin-bottom: 2rem
    }

    .header-contacts{
        flex-direction: column;
        justify-content: center;
    }

    .main-title{
        text-align: center;
        font-size: 3.2rem;
        padding-bottom: 4rem;
    }

    .main-subtitles-item{
        font-size: 1.5rem;
    }

    .main-form{
        padding: 2rem;
        form{
            input, button{
                font-size: 1.2rem;
            }

            button{
                padding: 2rem 4rem;
            }
        }
    }

    .services-item{
        padding: 4rem 3rem;

        &-title{
            font-size: 2.8rem;
        }

        &-descr{
            font-size: 1.4rem;
        }
    }

    .facts-descr{
        &-title{
            font-size: 3.5rem;
        }

        &-subtitle{
            font-size: 1.4rem;
        }
    }

    .license-subtitle{
        font-size: 1.6rem;    
    }

    .footer-title{
        font-size: 3rem;
    }

    .footer-subtitle{
        font-size: 1.6rem;
    }

    .footer-phone{
        font-size: 2.5rem;
    }

    .popup-order-content{
        padding: 2rem 3rem;
    }

    .popup-order-form{
        padding: 3rem 0;
    }

    .popup-order-close{
        top: -0.8rem;
        right: 0.5rem;
        color: #000;
    }

    .popup-thanks-close{
        top: -0.8rem;
        right: 0.5rem;
        color: #000;
    }
    .popup-audit{
        &-title{
            font-size: 2.5rem;
        }

        &-item{
            text-align: center;
        }

        &-close{
            top: -0.8rem;
            right: 0.5rem;
            color: #000; 
        }

        &-content{
            top: 5%;
        }

        &-btn{
            width: 100%;
        }

        &-price{
            text-align: center;
        }
    }

    .popup-trash{
        &-title{
            font-size: 2.5rem;
        }

        &-item{
            text-align: center;
        }

        &-close{
            top: -0.8rem;
            right: 0.5rem;
            color: #000; 
        }

        &-content{
            top: 5%;
        }

        &-btn{
            width: 100%;
        }

        &-price{
            text-align: center;
        }
    }
}